.uploadWrap{
  display: flex;
  gap:10px;
  align-items: center;
  flex-wrap: wrap;
}

.uploadBtn{
  display: flex;
  justify-content: center;
  align-items: center;
  cursor:pointer;
  font-size:1.8rem;
  font-weight: bold;
  @include commonBtn($secondary, white , 141px, 44px);
  span{
    margin-left:5px
  }
}

.fileInfor {
  line-height: 1.5;
  color :$gray;
  & p:nth-child(2){
    font-size: 1.6rem;
    color: #5d5d5d;
  }
}